import http from '/api'

export const getplanlist = (params = {}) =>
	http.fetchGet(`/plan/list`, params)

export const deleteplan = (params = {}) =>
	http.fetchGet(`/plan/delete`, params)

export const addplan = (params = {}) =>
	http.fetchPost(`/plan/add`, params, true)

export const updateplan = (params = {}) =>
	http.fetchPost(`/plan/update`, params, true)

export const getnotification = (params = {}) =>
	http.fetchGet(`/plan/getNotification`, params, true)

export const createorder = (params = {}) =>
	http.fetchGet(`/plan/planToOrder`, params, true)	

export const import_url = '/plan/importPlan'

