import React, { Component } from 'react'
import { Layout, Button, notification, List } from 'antd'
const { Content } = Layout

import Header from '/components/header.jsx'
import Sider from '/components/sider.jsx'
import Loading from '/components/loading.jsx'
import { getnotification } from '/api/plan'

const openNotification = (node) => {
  notification.open({
    message: '消息提醒',
    description: node,
    duration: null,
    style: {
      marginTop: 100
    },
    onClick: () => {
      console.log('Notification Clicked!')
    }
  })
}

const getNotification = _ => {
	getnotification({}).then(res => {
		let data = res.data.records
		if(data){
			let node = (<List
		    itemLayout="horizontal"
		    dataSource={data}
		    renderItem={item => (
		      <List.Item>
		        <List.Item.Meta
		          title={<a href="https://ant.design">{item.workShopName}-{item.equipmentName}</a>}
		          description={item.content}
		        />
		      </List.Item>	
		    )}
		  />)
			openNotification(node)
		}
	})
}
//getNotification()


class Home extends Component{
	render = _ => 
		<Layout className='wrapper'>
			<Sider />
			<Layout>
				<Header />
				<Content style = {{paddingLeft: 36}}>{this.props.children}</Content>
			</Layout>
		</Layout>
}

export default Home


