import React, { Component } from 'react'
import { Layout, Menu, Icon, Dropdown } from 'antd'
import { connect } from 'react-redux'
import { TOGGLE, ADD_PANE, RESET } from '/redux/action'
const { Header } = Layout
const SubMenu = Menu.SubMenu
import { hashHistory } from 'react-router'

@connect(state => ({
    collapsed: state.global.collapsed
}), dispath => ({
    toggle(key){dispath({type: TOGGLE})},
    add(pane){dispath({ type: ADD_PANE, data: pane })},
    reset(){dispath({ type: RESET })}
}))
class DHeader extends Component{

    componentWillMount = _ => {
        let username = localStorage.getItem('username')
        this.setState({username})
    }

    state = {
        username: ''
    }

    quit = _ => {
        localStorage.setItem('token', '')
        localStorage.setItem('userid', '')
        localStorage.setItem('username', '')
        this.props.reset()
        hashHistory.replace('/login')
    }

    changePass = _ => {
        let pane = {
            title: '修改密码', 
            key: 'changepas',
            url: `/#/changePassword`
        }

        this.props.add(pane)
    }

    render = _ => <Header 
    className="header">
        <Icon
        className="trigger"
        type={this.props.collapsed ? 'menu-unfold' : 'menu-fold'}
        onClick={this.props.toggle}/>
        <div className="settingwrap">
             <img 
             src="/static/images/avatar.png"
             className="avatar" />
            <span style={{marginRight: 15}}>欢迎，{this.state.username || '管理员'}</span>
            <Dropdown 
            overlay={<Menu>
                <Menu.Item onClick={this.changePass}>修改密码</Menu.Item>
            </Menu>}>
                <span>设置</span>
            </Dropdown>
            <span className="settings" onClick={this.quit}>退出</span>
        </div>
    </Header>
}

export default DHeader


